import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css'

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";
import ZoomStore from "../../stores/ZoomStore";
import GraphInfoStore from "../../stores/GraphInfoStore";
import LegendStore from "../../stores/LegendStore";
import InfoCardStore from "../../stores/InfoCardStore";

import './menu.css';

import icons from "../../icons/icons.svg";
import { menuIconsLight, menuIconsDark } from '../../utils/menuIcons';

export interface MenuProps { 
  appStore?: AppStore;
  graphStore?: GraphStore;
  zoomStore?: ZoomStore;
  graphInfoStore?: GraphInfoStore;
  legendStore?: LegendStore;
  infoCardStore?: InfoCardStore;
}

@inject("appStore", "graphStore" ,"zoomStore", "graphInfoStore", "legendStore", "infoCardStore")
@observer class Menu extends Component<MenuProps, {}> {
	textInput: any = React.createRef();

  render() {
    const appStore: AppStore = this.props.appStore;
    const graphStore: GraphStore = this.props.graphStore;
    const zoomStore: ZoomStore = this.props.zoomStore;
    const graphInfoStore: GraphInfoStore = this.props.graphInfoStore;
    const legendStore: LegendStore = this.props.legendStore;
    const infoCardStore: InfoCardStore = this.props.infoCardStore;
    
    const menuIcons: any = appStore.theme === 'dark' ? menuIconsLight : menuIconsDark;

    interface Option {
    	value: number;
    	label: string;
    }

    const options: Array<Option> = [
		  { value: 0, label: 'Общий режим' },
		  { value: 1, label: 'Окружение НП по выбранному узлу'}
		];

		const defaultOption: Option = options[graphStore.mode];

		const dataOptions: Array<Option> = [
		  { value: 0, label: 'Мониторы с электронно-лучевой трубкой' },
		  { value: 1, label: 'Коляски детские'},
		  { value: 2, label: 'Телефонные аппараты для сотовых сетей'}
		];

		const defaultDataOption: Option = dataOptions[graphStore.currentData];

		const DropdownArrow = () => {
			return (
				<svg className="dropdown__arrow" width="10" height="7">
		      <use xlinkHref={ `${icons}#dropdown-arrow` }></use>
		    </svg>
			)
		}

		const changeData = (value) => {
			graphStore.changeData(value);
			infoCardStore.setClose();
			graphInfoStore.setOpen();
		}

  	return (
	    <div className={ `header ${appStore.theme}` }>
		    <div className="header__left">
		      <div
		      	className={ `header__item ${graphInfoStore.open ? 'active' : ''}` } 
			      onClick = { graphInfoStore.changeInfoVisible }>
		        <svg width="19" height="14">
	            	<use xlinkHref={ `${icons}#menu` }></use>
	         	</svg>
		      </div>
		      <div 
		      	className={ `header__item ${legendStore.open ? 'active' : ''}` } 
			      onClick = { legendStore.changeLegendVisible }>
		        <img src={menuIcons.iconMenu2} alt="" />
		      </div>
		      <div 
			      className={ `header__item ${graphStore.direction === 0 ? 'active' : ''}` } 
			      onClick = { graphStore.setHorizontalDirection }>
		        <img src={menuIcons.iconMenu3} alt="" />
		      </div>
		      <div 
		      	className={ `header__item ${graphStore.direction === 90 ? 'active' : ''}` } 
			      onClick = { graphStore.setVerticalDirection }>
		        <img src={menuIcons.iconMenu4} alt="" />
		      </div>
		      <div >
		        <Dropdown 
				    	options={dataOptions} 
				    	onChange={ (option: Option) => { changeData(option.value); }} 
				    	value={defaultDataOption} 
				    	placeholder="Select an option" 
				    	arrowClosed={<DropdownArrow />} 
		 					arrowOpen={<DropdownArrow />}
			    	/>
		      </div>
		    </div> 
		    <div className="header__center">
		    	<Dropdown 
			    	options={options} 
			    	onChange={ (option: Option) => { graphStore.changeMode(option.value); }} 
			    	value={defaultOption} 
			    	placeholder="Select an option" 
			    	arrowClosed={<DropdownArrow />} 
	 					arrowOpen={<DropdownArrow />}
		    	/>
		    </div>
		    <div className="header__right">
		      <div className="header__item active" onClick = { appStore.changeTheme }>
		        <img src={menuIcons.iconMenu5} alt="" />
		      </div>
		      <div 
		      	className={ `header__item ${zoomStore.isOpen ? 'active' : ''}` }  
		      	onClick={ zoomStore.changeZoomVisible }>
		        <img src={menuIcons.iconMenu6} alt="" />
		      </div>
		      <div className="header__item header__item-zoom" onClick={ () => this.textInput.current.focus() }>
		        <input 
		        	ref={this.textInput} 
		        	type="text" 
		        	value={zoomStore.zoom} 
		        	onChange={ () => zoomStore.setZoom(parseInt(this.textInput.current.value)) } 
		        />
		      </div>
		      <div 
		      	className={ `header__item ${appStore.isFullScreen ? 'active' : ''}` }  
		      	onClick={ appStore.changeFullScreen }>
		        <img src={menuIcons.iconMenu8} alt="" /> 
		      </div>
		    </div>
		  </div>
		);
  }
}

export default Menu;
