import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import Draggable from 'react-draggable';

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";

import iconClose from '../../icons/icon_close.svg';
import './zvCard.css';

export interface ZvCardProps { 
  appStore?: AppStore;
  graphStore?: GraphStore;
}

@inject("appStore", "graphStore")
@observer class ZvCard extends Component<ZvCardProps, {}> {
  render() {
    const graphStore: GraphStore = this.props.graphStore;
    const appStore: AppStore = this.props.appStore;

    const ZvItem = ({zv}: {zv: any}) => {
      const activeClass = graphStore.currentZv && graphStore.currentZv.name === zv.name ? 'active' : ''
      return (
        <div 
          className={`potential-card__item ${activeClass}`}
          onClick={ () => { graphStore.changeZvVisible(zv) } }>
          {zv.name}
        </div>
      );
    }

    const zvs = graphStore.graphInfo.STATEMENT_INFO ? JSON.parse(graphStore.graphInfo.STATEMENT_INFO) : [];

    const zvItems = zvs.map((zv: any, i) => {
      return <ZvItem zv={zv} key={i} />
    });
    
    const activeClass = graphStore.currentZv ? 'active' : ''

    return (
      <Draggable>
        <div className = { `potential-card ${ graphStore.zvMode ? 'open' : 'close'} ${ appStore.theme } ${activeClass}` }>          
          <div className="potential-card__close" onClick={graphStore.changeZvMode}>
            <img src={iconClose} alt="" />
          </div>
          <div className="potential-card__title">
            Номера заявлений
          </div>
          <div className="potential-card__items">
            { zvItems }
          </div>
        </div>
      </Draggable>
		);
  }
}

export default ZvCard;
