import { Vertex, Edge, Data, PreparedData } from '../interface';

const convertBooleanData = (value: string | boolean): boolean => typeof value !== 'boolean'? value === 'true' : value;
const converArrayData = (value) => typeof value === 'string' ? JSON.parse(value) : value;

const prepareVertexTypes = (vertex): Vertex => {
	return{
    PK: vertex.PK,
		Level: +vertex.Level,
		DT_ID: vertex.DT_ID,
    P_NUM: +vertex.P_NUM,
    ID: vertex.ID,
    TYPE: +vertex.TYPE,
    STATUS: +vertex.STATUS,
    ROOT: convertBooleanData(vertex.ROOT),
    LEAF: convertBooleanData(vertex.LEAF),
    TAX_TYPE: +vertex.TAX_TYPE,
    TAX_TYPE_NAME: vertex.TAX_TYPE_NAME,
    CO_CODE: +vertex.CO_CODE,
    CO_NAME: vertex.CO_NAME,
    FULL_NAME: vertex.FULL_NAME,
    SHORT_NAME: vertex.SHORT_NAME,
    INN: +vertex.INN,
    KPP: +vertex.KPP,
    DEBET: +vertex.DEBET,
    CREDIT: +vertex.CREDIT,
    BALANCE: +vertex.BALANCE,
    IN_BR: +vertex.IN_BR,
    OUT_BR: +vertex.OUT_BR,
    BALANCE_BR: +vertex.BALANCE_BR,
    RISK_INFO: converArrayData(vertex.RISK_INFO),
    RISK_LEVEL: +vertex.RISK_LEVEL,
    COST_IN: +vertex.COST_IN,
    COST_OUT: +vertex.COST_OUT,
    PRICE_IN: +vertex.PRICE_IN,
    PRICE_OUT: +vertex.PRICE_OUT,
    KRC: +vertex.KRC,
    DAMAGE: +vertex.DAMAGE,
    OKPD2_LIST: +vertex.OKPD2_LIST,
    EDGES_IN: vertex.EDGES_IN,
    EDGES_OUT: vertex.EDGES_OUT,
    PRICE_MIN_IN: +vertex.PRICE_MIN_IN,
    PRICE_MAX_OUT: +vertex.PRICE_MAX_OUT,
    QUANTITY_MIN_IN: +vertex.QUANTITY_MIN_IN,
    QUANTITY_MAX_OUT: +vertex.QUANTITY_MAX_OUT,
    UNIT_CODE: +vertex.UNIT_CODE,
    TAX_AMOUNT_OUT: +vertex.TAX_AMOUNT_OUT,
    OPERATIONS: vertex.OPERATIONS,
    PRICE_AVG: +vertex.PRICE_AVG,
    ST_FLAG: convertBooleanData(vertex.ST_FLAG),
    LAST_DOC_DATE: +vertex.LAST_DOC_DATE,
    PRICE_MIN: +vertex.PRICE_MIN,
    PRICE_MAX: +vertex.PRICE_MAX,
    TNVED: +vertex.TNVED
	}
}

const prepareVertexesTypes = (vertexes): Array<Vertex> => {
	return vertexes.map((vertex) => prepareVertexTypes(vertex));
}

const prepareEdgeTypes = (edge): Edge => {
  return{
    PK: edge.PK,
    DT_ID: edge.DT_ID,
    P_NUM: +edge.P_NUM,
    OP_TYPE: +edge.OP_TYPE,
    EDGE_TYPE: +edge.EDGE_TYPE,
    OUT_CO_CODE: +edge.OUT_CO_CODE, 
    IN_CO_CODE: +edge.IN_CO_CODE,
    QUANTITY: +edge.QUANTITY,
    PRICE_MIN: +edge.PRICE_MIN,
    PRICE_MAX: +edge.PRICE_MAX,
    BROKER: convertBooleanData(edge.BROKER),
    VIO_FLAG: convertBooleanData(edge.VIO_FLAG),
    TAX_AMOUNT: +edge.TAX_AMOUNT,
    LAST_DOC_DATE: edge.LAST_DOC_DATE,
    SID: edge.SID,
    BID: edge.BID,
    OPERATIONS: edge.OPERATIONS,
    PRICE_AVG: +edge.PRICE_AVG,
    UPDATED: +edge.UPDATED,
    COST_RUB_NET: +edge.COST_RUB_NET,
    IPT: edge.IPT,
    EDGE_TYPE_NAME: edge.EDGE_TYPE_NAME,
    STATEMENT: edge.STATEMENT
  }
}

const prepareEdgesTypes = (edges): Array<Edge> => {
  return edges.map((edge) => prepareEdgeTypes(edge));
}

const prepareDataTypes = (data: Data): PreparedData => {
	return {
		vertexes: prepareVertexesTypes(data.vertexes),
		edges: prepareEdgesTypes(data.edges),
		graphInfo: data.graphInfo
	}
}

export { prepareDataTypes };