import { observable, configure, action, decorate } from 'mobx';
import * as go from 'gojs';

configure({ enforceActions: 'observed' });

class ZoomStore {
	diagram: go.Diagram | null = null;
	mapIsOpen: boolean = false;
	zoom = 100;
	isOpen: boolean = true;

	changeZoomVisible = (): void => {
		this.isOpen = !this.isOpen;
	}

	openMap = (): void => {
		this.mapIsOpen = true;
	}

	closeMap = (): void => {
		this.mapIsOpen = false;
	}

	initOverview = (): go.Overview => {
		return go.GraphObject.make(go.Overview, {
      contentAlignment: go.Spot.Center
    });
	}

  setZoom = (value: number): void => {
    if (value < 1) value = 1;
    if (value > 200) value = 200;
    this.zoom = value;
    this.diagram.scale = this.zoom / 100;
  }

  setZoomToFit = (): void => {
    this.diagram.zoomToFit();
    this.zoom = this.diagram.scale * 100;
  }

};

decorate(ZoomStore, {
	mapIsOpen: observable,
	isOpen: observable,
	openMap: action,
	closeMap: action,
	initOverview: action,
	zoom: observable,
  setZoom: action,
  setZoomToFit: action,
  changeZoomVisible: action
});

export default ZoomStore;