import * as go from 'gojs';

const getBackgroundColor = (theme: string): string => theme === 'dark' ? '#1d2336' : '#eef0f5';

const getGridColor = (theme: string): string => theme === 'dark' ? '#181e2f' : '#f2f4f7';

const getDefaultStrokeColor = (theme: string): string => theme === 'dark' ? '#5a6487' : '#BDC4DB';

const getNodeStrokeColor = (node, theme: string): string => {

	let strokeColor: string = getDefaultStrokeColor(theme);

  if (node.category === 'tech') {
    switch (node.TYPE) {
      case 24:
        strokeColor = '#5BB95B';
        break;
      case 12:
        strokeColor = '#31ABC4';
        break;
      case 20:
        strokeColor = '#859DE0';
        break;
      case 23:
        strokeColor = '#F09456';
        break;
      default:
        strokeColor = getDefaultStrokeColor(theme);
    }
  } else if (node.category === 'root') {
    //strokeColor = defaultStrokeColor;
  } else if (node.category === 'taxpayer'){
    if (node.RISK_INFO) {
      node.RISK_INFO.forEach((risk) => {
        if(risk.Code === 201){
          strokeColor = '#F2696D';
        }
      });
    }
  }

  return strokeColor;
 }

  const getEdgeStrokeColor = (edge: go.Link, theme: string): string => {
    let strokeColor = getDefaultStrokeColor(theme);

    if (edge.category === 'potential') {
      strokeColor = '#8805ff';
    }

    if (edge.category === 'clone') {
      strokeColor = '#5bb95b';
    }

    return strokeColor;
  }

  const getTextColor = (theme: string): string => {
    return theme === 'dark' ? '#ffffff' : '#2b3250';
  }

  export { getBackgroundColor, getGridColor, getDefaultStrokeColor, getNodeStrokeColor, getTextColor, getEdgeStrokeColor };