import { observable, configure, action, decorate } from 'mobx';

configure({ enforceActions: 'observed' });

class GraphInfoStore {
	open: boolean = true;

	changeInfoVisible = (): void => {
		this.open = !this.open;
	} 

	setOpen = (): void => {
		this.open = true;
	}

	setClose = (): void => {
		this.open = false;
	}

};

decorate(GraphInfoStore, {
  open: observable,
  changeInfoVisible: action,
  setClose: action,
  setOpen: action
});

export default GraphInfoStore;