import { observable, computed, configure, action, decorate } from 'mobx';
import * as go from 'gojs';

import { getBackgroundColor, getNodeStrokeColor, getGridColor, getTextColor, getEdgeStrokeColor } from '../utils/getColors';
import { prepareNode, prepareEdge } from '../utils/prepareData';
import LinkTemplates from '../utils/LinkTemplates';
import NodeTemplates from '../utils/NodeTemplates';
import onLayoutCompleted from '../utils/LayoutCompleted';

import AppStore from "./AppStore";
import InfoCardStore from "./InfoCardStore";
import RiskCardStore from "./RiskCardStore";

import { Vertex, Edge } from '../interface';

import data1 from '../utils/data';
import data2 from '../utils/data2';
import data3 from '../utils/data3';
import { prepareDataTypes } from '../utils/prepareDataTypes';

configure({ enforceActions: 'observed' });

export interface Country {
  SHORT_NAME: string;
  FULL_NAME: string;
}

export interface Countries {
  '392'?: Country,
  '398'?: Country,
  '643'?: Country
}

export interface hiddenNodesForMega {
  intoHiddenNodes: Array<go.Node>;
  outHiddenNodes: Array<go.Node>;
}

class GraphStore {
  appStore: AppStore;
  infoCardStore: InfoCardStore; 
  riskCardStore: RiskCardStore; 

  direction: number = 0;
  vertexes: Array<Vertex>;
  edges: Array<Edge>;
  graphInfo: any;
  allGraphInfo: any;

  nodeTemplates: NodeTemplates;
  linkTemplates: LinkTemplates;
 
  valueVisible: boolean;
  labelVisible: boolean;

  constructor(appStore: AppStore, infoCardStore: InfoCardStore, riskCardStore: RiskCardStore) {
    this.appStore = appStore;
    this.infoCardStore = infoCardStore;
    this.riskCardStore = riskCardStore;

    this.direction = appStore.options.direction !== 90 ? 0 : 90;
    this.vertexes = appStore.data.vertexes || [];
    this.edges = appStore.data.edges || [];
    this.allGraphInfo = appStore.data.graphInfo || [];
    this.graphInfo = appStore.data.graphInfo[0] || {};

    this.nodeTemplates = new NodeTemplates(this, infoCardStore, riskCardStore);
    this.linkTemplates = new LinkTemplates(infoCardStore);

    this.valueVisible = true;
    this.labelVisible = true;
  }

  diagram: go.Diagram = null;
  mode: number = 0;
  viewedNode: go.Node | null = null;
  visibleNodes: Array<string> = [];
  isRiskClick: boolean = false;
  riskCardIsOpen: boolean = false;
  currentRiskNode: go.Node | null = null; 
  currentRiskInfo: Array<any> = [];
  currentRisk: any = null;
  connectedRntp: Array<any> = []; 

  countries: Countries = {
    '392': {
      SHORT_NAME: 'JP',
      FULL_NAME: 'Япония'
    },
    '398': {
      SHORT_NAME: 'KZ',
      FULL_NAME: 'Казахстан'
    },
    '643': {
      SHORT_NAME: 'RU',
      FULL_NAME: 'Россия'
    }
  };

  nodeTypes: any = {
    "1": "Резидент КЗ",
    "2": "Резидент Армении",
    "3": "Резидент РБ",
    "4": "Резидент Киргизии",
    "5": "Резидент РФ",
    "6": "Зарегистрированная иностранная компания",
    "7": "Вывоз из РФ в ЕАЭС",
    "8": "Зарегистрированный остаток по РФ",
    "9": "Технический узел «Захоронение, обезвреживание, утилизация или уничтожение»",
    "10": "Технический узел «Использование в производстве, в том числев переработке»",
    "11": "Технический узел «Уничтожение или безвозвратная утрата товаров вследствие действия непреодолимой силы»",
    "12": "Технический узел «Вывод из состава основных средств»",
    '13': "Технический узел «Передача при выделении из уставного капитала»",
    "14": "Физ.лицо, не связанное с осуществлением предпринимательской деятельности",
    "15": "Технический узел «Передача или реализация в розничной торговле»",
    "16": "Операция 26",
    "17": "Резидент не член ЕАЭС",
    "18": "Технический узел «Выпуск товара по ДТ»",
    '19': "Ввоз из ЕАЭС в РФ",
    "20": "Технический узел «Фактический вывоз с таможенной территории РФ в соответствии с таможенной процедурой экспорта или реэкспорта»",
    "21": "Технический узел «Передача в уставной капитал»",
    "22": "Технический узел «Передача в счет погашения обязательств при банкротстве»",
    "23": "Технический узел «Конфискация государством»",
    "24": "Технический узел «Включение в состав основных средств»",
    "25": "Технический узел «Возврат товара из розницы»",
    "26": "Технический узел «Возврат утраченного товара»",
    "27": "Ввод в оборот конфискованного товара"
  };

  categories: Array<string> = ['taxpayer', 'taxpayerNo', 'tech_c', 'tech_18', 'tech_19', 'tech', 'clone', 'megaNode'];

  currentData = 0;

  changeData = (value) => {
    this.currentData = value;
    let graphData: any = prepareDataTypes(data1);
    switch(value){
      case 0: graphData = prepareDataTypes(data1); break;
      case 1: graphData = prepareDataTypes(data2); break;
      case 2: graphData = prepareDataTypes(data3); break;
      default: graphData = prepareDataTypes(data1); break;
    }
    this.vertexes = graphData.vertexes;
    this.edges = graphData.edges;
    this.allGraphInfo = graphData.graphInfo || [];
    this.graphInfo = graphData.graphInfo[0] || {};

    //this.valueVisible = true;
    //this.labelVisible = true;
    //this.mode = 0;
    this.viewedNode = null;
    this.visibleNodes = [];
    this.isRiskClick = false;
    this.riskCardIsOpen = false;
    this.currentRiskNode = null; 
    this.currentRiskInfo = [];
    this.currentRisk = null;
    this.connectedRntp = [];
    this.modeNoResidents = true;
    this.potentialMode = false;
    this.potentialNodes = null;
    this.currentPotentialNode = null;
    this.currentPotentialRisk = null;
    this.zvMode = false;
    this.currentZv = null;
    this.cloneMode = true;

    this.diagram.layout = this.diagramLayout;
    this.diagram.linkTemplate = this.linkTemplate;
    this.diagram.model = new go.GraphLinksModel(this.graphNodes, this.graphEdges);
  }

  get nodesWithClones(): Array<Vertex> {
    const nodes: Array<Vertex> = [...this.vertexes];

    this.edges.forEach((edge: Edge) => {
      const startNode: Vertex = nodes.find((node) => node.PK === edge.SID);
      const endNode: Vertex = nodes.find((node) => node.PK === edge.BID);

      if (edge.SID === edge.BID) {
        //nodes.push({ ...endNode, PK: endNode.PK + '_clone', isClone: true });
      }

      if (endNode.Level - startNode.Level > 1) {
        nodes.push({ ...endNode, PK: endNode.PK + '_clone', isClone: true, cloneFor: endNode.PK });
      }
    });

    return nodes;
  }

  get graphNodesWithZv(): Array<Vertex> {
    return this.nodesWithClones.map((node) => prepareNode(node, this.appStore.theme, this.graphInfo, this));
  }

  modeNoResidents = true;

  showResidents = () => {
    if(this.modeNoResidents){
      this.modeNoResidents = false;
      this.diagram.layout = this.diagramLayout;
      this.diagram.linkTemplate = this.linkTemplate;
      this.diagram.model = new go.GraphLinksModel(this.graphNodes, this.graphEdges);
    }
  }

  get rootIsType19(){
    return this.vertexes.find((vertex) => vertex.TYPE === 19 && vertex.ROOT);
  }

  private nodeIsNoresident = (node) => {
    if(node.TYPE === 1 || node.TYPE === 2 || node.TYPE === 3 || node.TYPE === 4){
      return true;
    }
    return false;
  }

  get graphNodes(): Array<Vertex> {
    const nodes = this.graphNodesWithZv.filter((node) => node.DT_ID === this.graphInfo.DT_ID);

    if(this.modeNoResidents && this.rootIsType19){
      return nodes.filter((node) => !this.nodeIsNoresident(node));
    }

    return nodes;
  }

  get rootNode(): Vertex {
    return this.graphNodes.find((node) => node.Level === 0);
  }

  get firstLevelNode(): Vertex {
    return this.graphNodes.find((node) => node.Level === 1);
  }

  get graphEdgesWithClones(): Array<Edge> {
    let edges: Array<Edge> = [];
    this.edges.forEach((edge) => edges.push({...edge}));

    const cycle = edges.findIndex((edge) => edge.SID === edge.BID); //
    if(cycle >= 0) edges.splice(cycle, 1); //

    edges.forEach((edge: Edge) => {
      const startNode: Vertex = this.graphNodes.find((node) => node.PK === edge.SID);
      const endNode: Vertex = this.graphNodes.find((node) => node.PK === edge.BID);

      if (edge.SID === edge.BID) {
        //edge.BID = edge.BID + '_clone';
        //edge.to = edge.BID + '_clone';
      }

      if (startNode && endNode && endNode.Level - startNode.Level > 1) {
        edges.forEach((mappedEdge) => {
          if(mappedEdge.BID === edge.BID && mappedEdge.PK !== edge.PK){
            mappedEdge.BID = edge.BID + '_clone';
            mappedEdge.to = edge.BID + '_clone';
          }
        });
      }
    });

    return edges;
  }

  get graphEdgesWithZv(): Array<Edge> {
    return this.graphEdgesWithClones.map((edge) => prepareEdge(edge, this.appStore.theme, this.graphNodesWithZv, this));
  }

  get graphEdges(): Array<Edge> {
    const edges = this.graphEdgesWithZv.filter((edge) => edge.DT_ID === this.graphInfo.DT_ID);

    if(this.modeNoResidents && this.rootIsType19){
      return edges.filter((edge) => {
        const startNode: Vertex = this.vertexes.find((node) => node.PK === edge.SID);
        const endNode: Vertex = this.vertexes.find((node) => node.PK === edge.BID);
        if(this.nodeIsNoresident(startNode) || this.nodeIsNoresident(endNode)){
          return false;
        }
        return true;
      });
    }

    return edges;
  }

  get linkTemplate(): go.Link {
    return this.direction === 0 ? this.linkTemplates.horizontal : this.linkTemplates.vertical;
  }

  get rootCount(): number {
    return this.vertexes.filter((vertex) => vertex.ROOT).length;
  }

  get layering(): any {
    return this.rootCount > 2 ? go.LayeredDigraphLayout.LayerLongestPathSource : go.LayeredDigraphLayout.LayerOptimalLinkLength;
  }

  get diagramLayout(): go.LayeredDigraphLayout{
    return go.GraphObject.make(
      go.LayeredDigraphLayout, {
        setsPortSpots: false,
        direction: this.direction,
        layerSpacing: 100,
        columnSpacing: 14,
        layeringOption: this.layering
      }
    );
  }

  get contentAlignment(): go.Spot {
    return this.direction === 0 ? new go.Spot(0, 0.5) : new go.Spot(0.5, 0);
  }

  get diagrammPadding(): go.Margin {
    return this.direction === 0 ? new go.Margin(0, 60) : new go.Margin(60, 0);
  }

  get grid(): go.Panel {
    const gridShape: string = this.direction === 0 ? 'BarV' : 'BarH';
    const gridCellSize: go.Size = this.direction === 0 ? new go.Size(340, 340) : new go.Size(184, 184);
    const gridOrigin: go.Point = this.direction === 0 ? new go.Point(-60, 0) : new go.Point(0, -60);

    return go.GraphObject.make(go.Panel, "Grid", {
        gridCellSize: gridCellSize,
        gridOrigin: gridOrigin
      },
      go.GraphObject.make(go.Shape, gridShape, {
        fill: getGridColor(this.appStore.theme),
        interval: 2
      })
    );
  }

  initDiagram = (): go.Diagram => {
    const $ = go.GraphObject.make;

    const diagram: go.Diagram =
      $(go.Diagram, {
        "undoManager.isEnabled": true,
        "animationManager.isEnabled": false,
        "hasHorizontalScrollbar": false,
        "hasVerticalScrollbar": false,
        "contentAlignment": this.contentAlignment,
        "padding": this.diagrammPadding,
        "grid.visible": true,
        "allowDelete": false,
        "allowMove": false,
        "layout": this.diagramLayout,
      });

    diagram.grid = this.grid;
    this.categories.forEach((category) => diagram.nodeTemplateMap.add(category, this.nodeTemplates[category]));
    diagram.linkTemplate = this.linkTemplate;

    diagram.addDiagramListener("LayoutCompleted", (e) => {
      if ((diagram.layout as go.LayeredDigraphLayout).direction === 0) {
        return onLayoutCompleted(this, diagram);
      }
    });

    return diagram;
  }

  private setGrid = (): void => {
    this.diagram.contentAlignment = this.contentAlignment;
    this.diagram.padding = this.diagrammPadding;
    this.diagram.grid = this.grid;
  }

  setHorizontalDirection = (): void => {
    this.direction = 0;
    this.changeDirection();
  }

  setVerticalDirection = (): void => {
    this.direction = 90;
    this.changeDirection();
  }

  private changeDirection = (): void => {
    this.diagram.layout = this.diagramLayout;
    this.diagram.linkTemplate = this.linkTemplate;
    this.setGrid();

    this.drawNodes();
  }

  private drawNodes = () => {
    this.hideClones();
    this.destroyZv();
    this.destroyPotential();
    this.destroyCollapse();
    //if (this.cloneMode) this.hideClones();
    if (this.cloneMode && (!this.viewedNode || this.mode === 0)) this.showClones();
    if (this.mode === 1 && this.viewedNode 
      && (!this.zvMode || !this.currentZv) 
      && (!this.potentialMode || !this.currentPotentialRisk)) this.setCollapse();
    if (this.zvMode && this.currentZv) this.setZv();
    if (this.potentialMode && this.currentPotentialRisk) this.setPotential();
    this.setGraphTheme();
  }

  changeMode = (value: number): void => {
    if (this.mode !== value) {
      this.mode = value;
      this.setMode();
    }
  }

  private setMode = (): void => {
    this.drawNodes();
  }

  changeCollapse = (selectedNode: go.Node): void => {
    if (this.viewedNode && this.viewedNode.key === selectedNode.key) {
      this.viewedNode = null;
    } else if (this.mode === 1) {
      this.viewedNode = selectedNode;
      this.createCollapseData();
    }
    this.drawNodes();
  }

  private createCollapseData = (): void => {
    this.hideClones();
    this.visibleNodes = [];

    this.diagram.nodes.each((currentNode) => {
      let isParent: boolean, isChildren: boolean = false;

      currentNode.findTreeParts().each((node) => {
        if (node.data.key === this.viewedNode.data.key) isParent = true;
      });

      currentNode.findNodesInto().each((node) => {
        if (node.data.key === this.viewedNode.data.key) isChildren = true;
      })

      if ((isParent || isChildren || currentNode.category === 'root') && !currentNode.data.hidden ) {
        this.visibleNodes.push(currentNode.data.key);
      }
    });
  }

  private setCollapse = (): void => {
    this.destroyCollapse();
    this.diagram.layout = new go.Layout();
    
    this.diagram.commit((d) => {
      this.hideInvisibleNodes(d);

      this.visibleNodes.forEach((nodeKey) => {
        const currentNode: go.Node = d.findNodeForKey(nodeKey);

        const intoHiddenNodes: Array<go.Node> = this.hiddenNodesForMega(d, currentNode).intoHiddenNodes;
        const outHiddenNodes: Array<go.Node> = this.hiddenNodesForMega(d, currentNode).outHiddenNodes;

        if (outHiddenNodes.length > 0 || intoHiddenNodes.length > 0) {
          this.createMegaNode(d, currentNode, outHiddenNodes, intoHiddenNodes);
        }
      });
    });
  }

  private destroyCollapse = (): void => {
    this.diagram.commit((d) => {
      d.nodes.each((node) => {
        if(!node.data.hidden){
          if (node.category === 'megaNode') {
            node.category = node.data.oldCategory;
            d.model.set(node.data, "oldCategory", undefined);
          }
          node.opacity = 1;
        }
      });

      d.links.each((link) => {
        if(!link.data.hidden){
          if (link.category === 'megaLink') {
            link.category = undefined;
            d.model.set(link.data, "label", link.data.oldLabel);
            d.model.set(link.data, "oldLabel", undefined);
          }
          link.opacity = 1;
        }
      });
    });

    this.setGraphTheme();
  }

  private nodeIsVisible = (node: go.Node): boolean => this.visibleNodes.some((nodeKey) => nodeKey === node.key);

  private hideInvisibleNodes = (d: go.Diagram): void => { 
    d.nodes.each((node) => {
      if (!this.nodeIsVisible(node)) {
        node.opacity = 0

        node.findLinksConnected().each((link) => {
          link.opacity = 0
        });
      }
    });
  }

  private hiddenNodesForMega = (d: go.Diagram, currentNode: go.Node): hiddenNodesForMega => {
    const intoHiddenNodes: Array<go.Node> = [];
    const outHiddenNodes: Array<go.Node> = [];

    currentNode.findNodesInto().each((node) => {
      if ((!this.nodeIsVisible(node) || node.category === 'megaNode') && !node.data.hidden) {
        intoHiddenNodes.push(node);
      }
    });

    currentNode.findNodesOutOf().each((node) => {
      let isReverse: boolean = false;

      node.findLinksBetween(currentNode).each((link) => {
        if (link.data.reverse) isReverse = true;
      });

      if ((!this.nodeIsVisible(node) && !isReverse) && !node.data.hidden) {
        outHiddenNodes.push(node);
      } else if ((!this.nodeIsVisible(node) && isReverse) && !node.data.hidden) {
        intoHiddenNodes.push(node);
      }
    });

    return {
      intoHiddenNodes,
      outHiddenNodes
    }
  }

  private createMegaNode = (d: go.Diagram, currentNode: go.Node, outHiddenNodes: Array<go.Node>, intoHiddenNodes: Array<go.Node>): void => {
    const megaIndex: number = Math.ceil((outHiddenNodes.length) / 2) - 1;
    let megaNode: go.Node = outHiddenNodes[megaIndex];
    if(outHiddenNodes.length === 0){
      megaNode = intoHiddenNodes[0];
    }
    const hiddenNodesCount: number = outHiddenNodes.length + intoHiddenNodes.length;

    d.model.set(megaNode.data, "oldCategory", megaNode.category);
    megaNode.category = 'megaNode';
    megaNode.opacity = 1;

    d.model.set(megaNode.data, "hiddenNodesCount", hiddenNodesCount);
    d.model.set(megaNode.data, "intoNodesCount", intoHiddenNodes.length);
    d.model.set(megaNode.data, "outNodesCount", outHiddenNodes.length);

    currentNode.findLinksTo(megaNode).each((link) => {
      link.category = 'megaLink';
      link.opacity = 1;

      d.model.set(link.data, "oldLabel", link.data.label);
      d.model.set(link.data, "label", '');

    });
  }

  openMegaNode = (node: go.Node): void => {
    node.findLinksInto().each((link) => {
      if (link.category === 'megaLink') {
        link.fromNode.findNodesOutOf().each((node) => {
          if(this.visibleNodes.indexOf(node.data.key) === -1 && !node.data.hidden) {
            this.visibleNodes.push(node.data.key);
          }
        });

        link.fromNode.findNodesInto().each((node) => {
          if (node.findNodesInto().count === 0 && this.visibleNodes.indexOf(node.data.key) === -1 && !node.data.hidden) {
            this.visibleNodes.push(node.data.key);
          }
        });
      }
    });

    //this.setCollapse();
    this.drawNodes();
  }

  setGraphTheme = (): void => { 
    this.diagram.commit((d) => {
      d.nodes.each((node) => {
        d.model.set(node.data, "strokeColor", getNodeStrokeColor(node.data, this.appStore.theme));
        d.model.set(node.data, "textColor", getTextColor(this.appStore.theme));
        d.model.set(node.data, "backgroundColor", getBackgroundColor(this.appStore.theme));
      });

      d.links.each((link) => {
        d.model.set(link.data, "startNodeColor", link.fromNode.data.strokeColor);
        d.model.set(link.data, "endNodeColor", link.toNode.data.strokeColor);
        d.model.set(link.data, "strokeColor", getEdgeStrokeColor(link.data, this.appStore.theme));
        d.model.set(link.data, "backgroundColor", getBackgroundColor(this.appStore.theme));
      });
    });
    this.setGrid();
  }

  changeRiskCardVisible = (node: go.Node): void => {
    this.isRiskClick = true;
    if (this.riskCardIsOpen && this.currentRiskNode.key === node.key) {
      this.closeRiskCard();
    } else {
      this.openRiskCard(node);
    }
    setTimeout(() => this.isRiskClick = false, 100);
  }

  openRiskCard = (node: go.Node): void => {
    this.riskCardIsOpen = true;
    this.currentRiskNode = node;
    this.currentRisk = null;
    this.currentRiskInfo = node.data.RISK_INFO;
  }

  closeRiskCard = (): void => {
    this.riskCardIsOpen = false;
    this.currentRiskNode = null;
    this.currentRiskInfo = [];
  }

  openRisk = (riskNum: number): void => {
    if (this.viewedNode && this.mode === 1) {
      this.destroyCollapse();
    }
    this.currentRisk = this.currentRiskInfo[riskNum];
    this.createRisk();
  }

  closeRisk = (): void => {
    this.destroyRisk();
    this.currentRisk = null;
    this.connectedRntp = [];
    if (this.viewedNode && this.mode === 1) {
      this.setCollapse();
    }
  }

  private createRisk = (): void => {
    this.destroyRisk();
    this.connectedRntp = this.currentRisk ? this.currentRisk.ConnectedRnpt || [] : [];
    if (this.currentRisk && this.currentRisk.ConnectedNodes) {
      this.diagram.layout = new go.Layout();

      this.diagram.model.commit((m) => {
        m.nodeDataArray.forEach((data) => {
          const nodeIsConnected: go.Node = this.currentRisk.ConnectedNodes.find((node) => node === data.ID);

          if (nodeIsConnected && data.category !== 'clone') {
            this.diagram.findNodeForKey(data.key).opacity = 1;
            (m as go.GraphLinksModel).addLinkData({
              from: this.currentRiskNode.data.key,
              to: data.key,
              strokeColor: '#8805ff',
              category: 'potential'
            });
          } else if (this.currentRiskNode.data.key === data.key) {
            this.diagram.findNodeForKey(data.key).opacity = 1;
          } else {
            this.diagram.findNodeForKey(data.key).opacity = 0.1;
          }
        });

        (m as go.GraphLinksModel).linkDataArray.forEach((data) => {
          if (data.category !== 'potential') {
            this.diagram.findLinkForData(data).opacity = 0.1;
          }
        });
      });
    }
    this.setGraphTheme();
  }

  private destroyRisk = (): void => {
    this.diagram.links.each((link) => {
      if (link.category === 'potential') {
        this.diagram.remove(link);
      } else {
        link.opacity = 1;
      }
    });
    this.diagram.model.commit((m) => {
      m.nodeDataArray.forEach((data) => {
        this.diagram.findNodeForKey(data.key).opacity = 1;
      });
    });
  }

  potentialMode = false;
  potentialNodes = null;
  currentPotentialNode = null;
  currentPotentialRisk: any = null;

  changePotentialMode = () => {
    if(this.potentialMode){
      this.potentialMode = false;

    }else{
      this.potentialMode = true;
      this.zvMode = false;
    }
    this.drawNodes();
  }

  changePotentialVisible = (node: any, risk: any): void => {
    if (this.currentPotentialRisk && this.currentPotentialRisk.RiskId === risk.RiskId) {
      this.currentPotentialNode = null;
      this.currentPotentialRisk = null;
      this.potentialNodes = null;
      
    } else {
      this.currentPotentialNode = node;
      this.currentPotentialRisk = risk;
      this.potentialNodes = risk.ConnectedNodes;
      
    }
    this.drawNodes();
  }

  private setPotential = (): void => {
   // this.destroyPotential();
    
      this.diagram.layout = new go.Layout();

      this.diagram.model.commit((m) => {
        m.nodeDataArray.forEach((data) => {
          const nodeIsConnected: go.Node = this.potentialNodes.find((node) => node === data.ID);

          if (nodeIsConnected && data.category !== 'clone' && !data.hidden) {
            this.diagram.findNodeForKey(data.key).opacity = 1;
            (m as go.GraphLinksModel).addLinkData({
              from: this.currentPotentialNode.key,
              to: data.key,
              strokeColor: '#8805ff',
              category: 'potential'
            });
          } else if (this.currentPotentialNode.key === data.key && !data.hidden) {
            this.diagram.findNodeForKey(data.key).opacity = 1;
          } else if(data.hidden){
            this.diagram.findNodeForKey(data.key).opacity = 0;
          }else{
            this.diagram.findNodeForKey(data.key).opacity = 0.1;
          }
        });

        (m as go.GraphLinksModel).linkDataArray.forEach((data) => {
          if (data.category !== 'potential' && !data.hidden) {
            this.diagram.findLinkForData(data).opacity = 0.1;
          }else if (data.hidden){
            this.diagram.findLinkForData(data).opacity = 0;
          }
        });
      });
  }

  private destroyPotential = (): void => {
    this.diagram.links.each((link) => {
      if (link.category === 'potential') {
        this.diagram.remove(link);
      } else if (link.data.hidden) {
        link.opacity = 0;
      }else{
        link.opacity = 1;
      }
    });
    this.diagram.model.commit((m) => {
      m.nodeDataArray.forEach((data) => {
        if (!data.hidden) {
          this.diagram.findNodeForKey(data.key).opacity = 1;
        }else{
          this.diagram.findNodeForKey(data.key).opacity = 0;
        }
      });
    });
  }

  zvMode = false;
  currentZv: any = null;

  changeZvMode = () => {
    if(this.zvMode){
      this.zvMode = false;
    }else{
      this.zvMode = true;
      this.potentialMode = false;
    }
    this.drawNodes();
  }

  changeZvVisible = (zv: any): void => {
    if (this.currentZv && this.currentZv.name === zv.name) {
      this.currentZv = null;
    } else {
      this.currentZv = zv;
      //this.setZv();   
    }
    this.drawNodes();
  }

  private setZv = (): void => {
    this.diagram.layout = new go.Layout();

     this.diagram.model.commit((m) => {
      this.diagram.nodes.each((node) => {
        node.opacity = 0.1;
      });

      const newGraph = this.allGraphInfo.find((graph) => graph.PK === this.currentZv.key);

      this.diagram.links.each((link) => {
        if (link.data.STATEMENT === this.currentZv.key) {
          link.opacity = 1;

          const fromNode = link.fromNode;
          const newFromNode = this.graphNodesWithZv.find((vertex) => vertex.ID === fromNode.data.ID && vertex.DT_ID === newGraph.DT_ID);
          fromNode.opacity = 1;
          m.set(fromNode.data, "BALANCE", newFromNode.BALANCE);
          m.set(fromNode.data, "RISK_INFO", newFromNode.RISK_INFO);
          m.set(fromNode.data, "valueIcon", newFromNode.valueIcon);
          m.set(fromNode.data, "isRisk", newFromNode.isRisk);
          m.set(fromNode.data, "isBalanceRisk", newFromNode.isBalanceRisk);

          const toNode = link.toNode;
          const newToNode = this.graphNodesWithZv.find((vertex) => vertex.ID === toNode.data.ID && vertex.DT_ID === newGraph.DT_ID);
          toNode.opacity = 1;
          m.set(toNode.data, "BALANCE", newToNode.BALANCE);
          m.set(toNode.data, "RISK_INFO", newToNode.RISK_INFO);
          m.set(toNode.data, "valueIcon", newToNode.valueIcon);
          m.set(toNode.data, "isRisk", newToNode.isRisk);
          m.set(toNode.data, "isBalanceRisk", newToNode.isBalanceRisk);
        } else {
          link.opacity = 0.1;
        }
      });
    });
  }

  private destroyZv = (): void => {
     this.diagram.model.commit((m) => {
      this.diagram.nodes.each((node) => {
        node.opacity = 1;
        const oldNode = this.graphNodesWithZv.find((vertex) => vertex.ID === node.data.ID && vertex.DT_ID === this.graphInfo.DT_ID);
        m.set(node.data, "BALANCE", oldNode.BALANCE);
        m.set(node.data, "RISK_INFO", oldNode.RISK_INFO);
        m.set(node.data, "valueIcon", oldNode.valueIcon);
        m.set(node.data, "isRisk", oldNode.isRisk);
        m.set(node.data, "isBalanceRisk", oldNode.isBalanceRisk);   
      });

      this.diagram.links.each((link) => {
       link.opacity = 1;
      });
    });
  }

  cloneMode = false;

  changeCloneMode = () => {
    if(this.cloneMode){
      this.cloneMode = false;
      //this.hideClones();
      
    }else{
      this.cloneMode = true;
     //this.showClones();

    }
    this.drawNodes();
  }

  hideClones = () => {
    this.diagram.links.each((link) => {
      if (link.category === 'clone') {
        this.diagram.remove(link);
      }
    });
  }

  showClones = () => {
    this.diagram.layout = new go.Layout();
    this.diagram.commit((d) => {
      d.nodes.each((node) => {
        if (node.data.isClone) {
          (d.model as go.GraphLinksModel).addLinkData({
              from: node.data.cloneFor,
              to: node.key,
              strokeColor: '#5bb95b',
              strokeDashArray: [4, 4],
              category: 'clone'
          });
        }
      });
    });  
  }
 
 changeValueVisible = () =>{
   if(this.valueVisible){
      this.valueVisible = false;
      this.hideValues();
   }else{
     this.valueVisible = true;
     this.showValues();
   }
 }

  showValues = () => {
    this.diagram.commit((d) => {
      d.nodes.each((node) => d.model.set(node.data, "valueVisible", true));
    });  
  }

  hideValues = () => {
    this.diagram.commit((d) => {
      d.nodes.each((node) => d.model.set(node.data, "valueVisible", false));
    });  
  }

  changeLabelVisible = () =>{
   if(this.labelVisible){
      this.labelVisible = false;
      this.hideLabels();
   }else{
     this.labelVisible = true;
     this.showLabels();
   }
 }

 showLabels = () => {
    this.diagram.commit((d) => {
      d.links.each((link) => d.model.set(link.data, "labelVisible", true));
    });  
  }

  hideLabels = () => {
    this.diagram.commit((d) => {
      d.links.each((link) => d.model.set(link.data, "labelVisible", false));
    });  
  }

};

decorate(GraphStore, {
  vertexes: observable,
  edges: observable,
  direction: observable,
  mode: observable,
  riskCardIsOpen: observable,
  currentRiskNode: observable,
  currentRiskInfo: observable,
  currentRisk: observable,
  viewedNode: observable,
  visibleNodes: observable,
  potentialMode: observable,
  currentPotentialRisk: observable,
  cloneMode: observable,
  valueVisible: observable,
  labelVisible: observable,
  zvMode: observable,
  currentZv: observable,
  modeNoResidents: observable,
  graphInfo: observable,
  graphNodes: computed,
  graphEdges: computed,
  rootNode: computed,
  firstLevelNode: computed,
  linkTemplate: computed,
  diagramLayout: computed,
  contentAlignment: computed,
  diagrammPadding: computed,
  changeCollapse: action,
  changeMode: action,
  openRisk: action,
  closeRisk: action,
  changeRiskCardVisible: action,
  closeRiskCard: action,
  setHorizontalDirection: action,
  setVerticalDirection: action,
  openMegaNode: action,
  changePotentialMode: action,
  changePotentialVisible: action,
  changeCloneMode: action,
  changeValueVisible: action,
  changeLabelVisible: action,
  changeZvVisible: action,
  changeZvMode: action,
  showResidents: action,
  changeData: action
});

export default GraphStore;