import { observable, configure, action, decorate } from 'mobx';
import * as go from 'gojs';
import AppStore from "./AppStore";

configure({ enforceActions: 'observed' });

class InfoCardStore {
	onVertexDetail: any;

	constructor(appStore:AppStore) {
		this.onVertexDetail = appStore.events.onVertexDetail;
	}

	open: boolean = false;
	collapsed: boolean = false;
	selectedNode: go.Node | null = null;
	selectedLink: go.Link | null = null;

	setOpen = (node: go.Node | null, link: go.Link | null): void => {
		this.open = true;
		this.selectedNode = node;
		this.selectedLink = link;
		//if(node) this.onVertexDetail(node.topIndex, node.leftIndex);
		if(node) console.log(node.data);
		if(link) console.log(link.data);
	}

	setClose = (): void => {
		this.open = false;
	}

	changeCollapsed = (): void => {
		this.collapsed = !this.collapsed;
	}

	unsetCollapsed = (): void => {
		this.collapsed = false;
	}
};

decorate(InfoCardStore, {
  open: observable,
  selectedNode: observable,
  selectedLink: observable,
  collapsed: observable,
  setOpen: action,
  setClose: action,
  changeCollapsed: action,
  unsetCollapsed: action
});

export default InfoCardStore;