import React, { Component } from "react";
import ReactDOM from 'react-dom';
import { Provider } from "mobx-react";
import App from "./components/app";
import AppStore from "./stores/AppStore";
import { prepareDataTypes } from './utils/prepareDataTypes';
import { Options, Data, Events } from './interface';
import data from './utils/data';

export interface GoGraphProps {
	options?: Options;
	data?: Data;
	onVertexDetail(id: string): void;
}

class GoGraph extends Component<GoGraphProps, {}>{
	private events: Events = {
		onVertexDetail: this.props.onVertexDetail
	}

	private appStore: AppStore = new AppStore(
		this.props.options, 
		prepareDataTypes(this.props.data),
		this.events
	);	

	get stores(): object {
		return {
		  appStore: this.appStore, 
		  graphStore: this.appStore.GraphStore,
		  menuStore: this.appStore.MenuStore,
		  rightMenuStore: this.appStore.RightMenuStore,
		  infoCardStore: this.appStore.InfoCardStore,
		  riskCardStore: this.appStore.RiskCardStore,
		  zoomStore: this.appStore.ZoomStore,
		  graphInfoStore: this.appStore.GraphInfoStore,
		  legendStore: this.appStore.LegendStore
		};
	}

	render(){
		return(
			<Provider {...this.stores} >
				<App />
			</Provider>
		)
	}
}

const options = {
	theme: 'dark',
	direction: 0
}

const onVertexDetail = (id) => {
	console.log('ss', id)
} 

ReactDOM.render(
	<GoGraph options={options} data={data} onVertexDetail={ onVertexDetail } />, 
	document.getElementById('root')
);

export default GoGraph;
