import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { ReactDiagram } from 'gojs-react';

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";
import ZoomStore from "../../stores/ZoomStore";

import { Vertex, Edge } from '../../interface';

import './graph.css';

export interface GraphProps { 
  appStore?: AppStore;
  graphStore?: GraphStore;
  zoomStore?: ZoomStore;
}

@inject("graphStore", "appStore", "zoomStore")
@observer class Graph extends Component<GraphProps, {}> {
  graphStore: GraphStore = this.props.graphStore;
  appStore: AppStore = this.props.appStore;
  zoomStore: ZoomStore = this.props.zoomStore;
  diagramRef: any = React.createRef();

  componentDidMount(){
    this.graphStore.diagram = this.diagramRef.current.getDiagram();
    this.zoomStore.diagram = this.diagramRef.current.getDiagram();
  }

  componentDidUpdate(){
    this.graphStore.setGraphTheme();
  } 

  graphNodes: Array<Vertex> = this.graphStore.graphNodes.map((node) => node);
  graphEdges: Array<Edge> = this.graphStore.graphEdges.map((edge) => edge);
  
  render() {
    return (
      <ReactDiagram
        divClassName={ 'diagram-component ' + this.appStore.theme }
        ref={this.diagramRef}
        initDiagram={this.graphStore.initDiagram }
        nodeDataArray={this.graphNodes}
        linkDataArray={this.graphEdges}
        onModelChange={() => {}}
        skipsDiagramUpdate={false}
      />
		);
  }
}

export default Graph;