import { observable, configure, action, decorate } from 'mobx';

configure({ enforceActions: 'observed' });

class LegendStore {
	open: boolean = false;

	changeLegendVisible = (): void => {
		this.open = !this.open;
	} 

	setClose = (): void => {
		this.open = false;
	}

};

decorate(LegendStore, {
  open: observable,
  changeLegendVisible: action,
  setClose: action
});

export default LegendStore;