import * as go from 'gojs';
import GraphStore from "../stores/GraphStore";

const onLayoutCompleted = (graphStore: GraphStore, diagram: go.Diagram): void => {
  diagram.commit((m) => {
    if (m.nodes.count) {
      m.nodes.each((node) => {
        if (node.location.x === 0) {
          node.location = new go.Point(node.location.x + 340, node.location.y - 220);
        }
        if (graphStore.firstLevelNode && node.data.key === graphStore.firstLevelNode.key && node.findNodesOutOf().count > 1) {
         // node.location = new go.Point(node.location.x, node.location.y - 85);
        }
        if (node.category === 'tech') {
          node.location = new go.Point(node.location.x - 82, node.location.y);
        }
        if (node.category === 'megaNode') {
          node.location = new go.Point(node.location.x - 50, node.location.y);
        }
      });
    }
  });
}

export default onLayoutCompleted;