import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import Draggable from 'react-draggable';

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";

import iconClose from '../../icons/icon_close.svg';
import iconNegative from '../../icons/icon_negative.svg';
import './potentialCard.css';

export interface PotentialCardProps { 
  appStore?: AppStore;
  graphStore?: GraphStore;
}

@inject("appStore", "graphStore")
@observer class PotentialCard extends Component<PotentialCardProps, {}> {
  render() {
    const graphStore: GraphStore = this.props.graphStore;
    const appStore: AppStore = this.props.appStore;

    const PotentialCardItem = ({node, risk}: {node: any, risk: any}) => {
      const activeClass = graphStore.currentPotentialRisk && graphStore.currentPotentialRisk.RiskId === risk.RiskId ? 'active' : ''
      return (
        <div 
          className={`potential-card__item ${activeClass}`}
          onClick={ () => { graphStore.changePotentialVisible(node, risk) } }>
          <div className="potential-card__icon">
            <img src={iconNegative} alt="" />
          </div>
          <div className="potential-card__info">
            <div className="potential-card__nodename">{risk.Name}</div>
            <div className="potential-card__riskname">{node.SHORT_NAME}</div>
          </div>
        </div>
      );
    }

    const potentialCardItems =  graphStore.graphNodes.map((node: any, i) => {
      let negativeRisk = null;
      if(node.RISK_INFO){
        negativeRisk = node.RISK_INFO.find((risk) => risk.Code === 201 && risk.ConnectedNodes.length > 0);
        if(negativeRisk){
          return <PotentialCardItem node={node} risk={negativeRisk} key={i} />
        }
      }
      return null;
    });
    
    const activeClass = graphStore.currentPotentialRisk ? 'active' : ''

    return (
      <Draggable>
        <div className = { `potential-card ${ graphStore.potentialMode ? 'open' : 'close'} ${ appStore.theme } ${activeClass}` }>          
          <div className="potential-card__close" onClick={graphStore.changePotentialMode}>
            <img src={iconClose} alt="" />
          </div>
          <div className="potential-card__title">
            Потенциальные контрагенты
          </div>
          <div className="potential-card__items">
            { potentialCardItems }
          </div>
        </div>
      </Draggable>
		);
  }
}

export default PotentialCard;
