import * as go from 'gojs';

class VerticalLink extends go.Link {

  computeMidOrthoPosition = (fromPosition, toPosition, vertical) => fromPosition;

  computePoints = (): boolean => {
    this.clearPoints();

    const fromNode: go.Node = this.fromNode;
    const toNode: go.Node = this.toNode;

    const fromNodeChildrens: go.Iterator<go.Node> = fromNode.findNodesOutOf();
    const toNodeParents: go.Iterator<go.Node> = toNode.findNodesInto();
    const fromNodeOutLinks: go.Iterator<go.Link> = fromNode.findLinksOutOf();

    if (toNode.location.y) {
      if (this.category !== 'potential') {
        this.addPoint(
          new go.Point(
            fromNode.location.x + fromNode.width / 2,
            fromNode.location.y + fromNode.height - 8
          )
        );

        this.addPoint(
          new go.Point(
            fromNode.location.x + fromNode.width / 2,
            fromNode.location.y + fromNode.height + 20
          )
        );

        if (fromNodeChildrens.count > 1) {
          let leftCount = 0;
          let rightCount = 0;

          fromNodeChildrens.each((node) => {
            if (node.location.x < fromNode.location.x) leftCount++;
            if (node.location.x > fromNode.location.x) rightCount++;
          });

          if ((leftCount > 0 && rightCount > 0) || fromNodeOutLinks.count > 1) {
            this.addPoint(
              new go.Point(
                fromNode.location.x + fromNode.width / 2 - 1,
                fromNode.location.y + fromNode.height + 20
              )
            );

            this.addPoint(
              new go.Point(
                fromNode.location.x + fromNode.width / 2 + 1,
                fromNode.location.y + fromNode.height + 20
              )
            );
          }
        }

        let lastPointY, lastPointX;
        if (toNodeParents.count > 1 && fromNode.location.x < toNode.location.x && toNode.category !== 'megaNode') {
          lastPointX = toNode.location.x + 32;
          lastPointY = toNode.location.y + 8;
        } else if (toNodeParents.count > 1 && fromNode.location.x > toNode.location.x && toNode.category !== 'megaNode') {
          lastPointX = toNode.location.x + toNode.width - 32;
          lastPointY = toNode.location.y + 8;
        } else {
          lastPointX = toNode.location.x + toNode.width / 2;
          lastPointY = toNode.location.y + 8;
        }

        if(fromNode.findLinksBetween(toNode).count > 1 && toNode.category !== 'megaNode'){
            let isToLeft, isToCenter = false;

            fromNode.findLinksBetween(toNode).each((link) => {
              if(link.data.connect === 'toLeft' && link !== this) isToLeft = true; 
              if(link.data.connect === 'toCenter' && link !== this) isToCenter = true; 
            });

            if(!isToLeft){
              this.data.connect = 'toLeft';
              //this.diagram.commit((d) => d.model.set(this.data, "connect", 'toTop'));
              lastPointX = toNode.location.x + 32;
              lastPointY = toNode.location.y + 8;
            }else if(!isToCenter){
              this.data.connect = 'toCenter';
              //this.diagram.commit((d) => d.model.set(this.data, "connect", 'toCenter'));
              lastPointX = toNode.location.x + toNode.width / 2;
              lastPointY = toNode.location.y + 8;
            }else{
              lastPointX = toNode.location.x + toNode.width - 32;
              lastPointY = toNode.location.y + 8;
            }

            this.addPoint(
              new go.Point(
                fromNode.location.x + fromNode.width / 2 - 1,
                fromNode.location.y + fromNode.height + 20
              )
            );

            this.addPoint(
              new go.Point(
                fromNode.location.x + fromNode.width / 2 + 1,
                fromNode.location.y + fromNode.height + 20
              )
            );
          }

        this.addPoint(
          new go.Point(
            lastPointX,
            fromNode.location.y + fromNode.height + 20
          )
        );

        this.addPoint(
          new go.Point(
            lastPointX,
            lastPointY
          )
        );
      } else {
        if (fromNode.location.y === toNode.location.y) {
          if (fromNode.location.x > toNode.location.x) {
            this.addPoint(
              new go.Point(
                fromNode.location.x + 8,
                fromNode.location.y + fromNode.height / 2
              )
            );

            this.addPoint(
              new go.Point(
                toNode.location.x + toNode.width - 8,
                toNode.location.y + toNode.height / 2
              )
            );
          } else {
            this.addPoint(
              new go.Point(
                fromNode.location.x + fromNode.width - 8,
                fromNode.location.y + fromNode.height / 2
              )
            );

            this.addPoint(
              new go.Point(
                toNode.location.x + 8,
                toNode.location.y + toNode.height / 2
              )
            );
          }
        } else if (fromNode.location.y < toNode.location.y) {
          this.addPoint(
            new go.Point(
              fromNode.location.x + fromNode.width / 2,
              fromNode.location.y + fromNode.height - 8
            )
          );

          this.addPoint(
            new go.Point(
              fromNode.location.x + fromNode.width / 2,
              fromNode.location.y + fromNode.height + 20
            )
          );

          this.addPoint(
            new go.Point(
              toNode.location.x + toNode.width / 2,
              fromNode.location.y + fromNode.height + 20
            )
          );

          this.addPoint(
            new go.Point(
              toNode.location.x + toNode.width / 2,
              toNode.location.y + 8
            )
          );
        } else if (fromNode.location.y > toNode.location.y) {
          this.addPoint(
            new go.Point(
              fromNode.location.x + fromNode.width / 2,
              fromNode.location.y + 8
            )
          );

          this.addPoint(
            new go.Point(
              fromNode.location.x + fromNode.width / 2,
              fromNode.location.y - 20
            )
          );

          this.addPoint(
            new go.Point(
              toNode.location.x + toNode.width / 2,
              fromNode.location.y - 20
            )
          );

          this.addPoint(
            new go.Point(
              toNode.location.x + toNode.width / 2,
              toNode.location.y + toNode.height - 8
            )
          );
        }
      }
    }

    return true;
  };

}

export default VerticalLink;