import * as go from 'gojs';
import HorizontalLink from './HorizontalLink';
import VerticalLink from './VerticalLink';
import InfoCardStore from "../stores/InfoCardStore";

const $ = go.GraphObject.make;

class LinkTemplates {
  infoCardStore: InfoCardStore;

  constructor(infoCardStore: InfoCardStore){
    this.infoCardStore = infoCardStore;
  }

  horizontal: go.Link =
    $(HorizontalLink, {
        routing: go.Link.Orthogonal,
        corner: 13,
        fromEndSegmentLength: 20
      },
      $(go.Shape, {
          strokeWidth: 2,
          stroke: "#5a6487",
          cursor: "pointer"
        },
        new go.Binding("stroke", "strokeColor"),
        new go.Binding("strokeDashArray", "strokeDashArray")
      ),
      $(go.Shape, {
          fromArrow: "Circle",
          scale: 1.4,
          strokeWidth: 2,
          stroke: "#1d2336",
          fill: "#5a6487"
        },
        new go.Binding("fill", "startNodeColor"),
        new go.Binding("stroke", "backgroundColor")
      ),
      $(go.Shape, {
          toArrow: "Circle",
          scale: 1.4,
          strokeWidth: 2,
          stroke: "#1d2336",
          fill: "#5a6487"
        },
        new go.Binding("fill", "endNodeColor"),
        new go.Binding("stroke", "backgroundColor")
      ),
      $(go.TextBlock, "", {
          segmentOffset: new go.Point(-60, -12),
          segmentIndex: NaN,
          segmentFraction: 1,
          stroke: "#859DE0",
          font: "bold 14px Fira Sans"
        },
        new go.Binding("text", "label"),
        new go.Binding("segmentOffset", "isFromRoot", (isFromRoot) => {
          return isFromRoot ? new go.Point(-85, -33) : new go.Point(-60, -12)
        }),
        new go.Binding("visible", "labelVisible", (labelVisible) => labelVisible ? true : false)
      ),
      {
        click: (e, link: go.Link) => {
          if(link.category !== 'clone' && link.category !== 'potential'){
            this.infoCardStore.setOpen(null, link);
          }
        }
      }
    );

  vertical: go.Link =
    $(VerticalLink, {
        routing: go.Link.Orthogonal,
        corner: 12,
        fromEndSegmentLength: 20
      },
      $(go.Shape, {
          strokeWidth: 2,
          stroke: "#5a6487",
          cursor: "pointer"
        },
        new go.Binding("stroke", "strokeColor"),
        new go.Binding("strokeDashArray", "strokeDashArray")
      ),
      $(go.Shape, {
          fromArrow: "Circle",
          scale: 1.4,
          strokeWidth: 2,
          stroke: "#1d2336",
          fill: "#5a6487"
        },
        new go.Binding("fill", "startNodeColor"),
        new go.Binding("stroke", "backgroundColor")
      ),
      $(go.Shape, {
          toArrow: "Circle",
          scale: 1.4,
          strokeWidth: 2,
          stroke: "#1d2336",
          fill: "#5a6487"
        },
        new go.Binding("fill", "endNodeColor"),
        new go.Binding("stroke", "backgroundColor")
      ),
      $(go.TextBlock, "", {
          segmentOffset: new go.Point(-50, -27),
          segmentIndex: NaN,
          segmentFraction: 1,
          stroke: "#859DE0",
          font: "bold 14px Fira Sans"
        },
        new go.Binding("text", "label"),
        new go.Binding("segmentOffset", "label", (label) => new go.Point(-50, -label.length * 5)),
        new go.Binding("visible", "labelVisible", (labelVisible) => labelVisible ? true : false)
      ),
      {
        click: (e, link: go.Link) => {
          if(link.category !== 'clone' && link.category !== 'potential'){
            this.infoCardStore.setOpen(null, link);
          }
        }
      }
    );
}

export default LinkTemplates;