import * as go from 'gojs';

const MyRoundedRectangle = (shape: go.Shape, w: number, h: number): go.Geometry => {
  let p1: number = 5;

  if (shape !== null) {
    const param1: number = shape.parameter1;
    if (!isNaN(param1) && param1 >= 0) p1 = param1;
  }

  p1 = Math.min(p1, w / 2);
  p1 = Math.min(p1, h / 2);

  const geo: go.Geometry = new go.Geometry();
  
  geo.add(new go.PathFigure(0, p1)
     .add(new go.PathSegment(go.PathSegment.Arc, 180, 90, p1, p1, p1, p1))
     .add(new go.PathSegment(go.PathSegment.Line, w - p1, 0))
     .add(new go.PathSegment(go.PathSegment.Arc, 270, 90, w - p1, p1, p1, p1))
     .add(new go.PathSegment(go.PathSegment.Line, w, h - p1))
     .add(new go.PathSegment(go.PathSegment.Arc, 0, 90, w - p1, h - p1, p1, p1))
     .add(new go.PathSegment(go.PathSegment.Line, p1, h))
     .add(new go.PathSegment(go.PathSegment.Arc, 90, 90, p1, h - p1, p1, p1).close()));

  return geo;
}

export default MyRoundedRectangle;