import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Scrollbars } from 'react-custom-scrollbars';

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";
import GraphInfoStore from "../../stores/GraphInfoStore";

import './graphInfo.css';

import iconClose from '../../icons/icon_close.svg';

export interface GraphInfoProps { 
  appStore?: AppStore;
  graphStore?: GraphStore;
  graphInfoStore?: GraphInfoStore;
}

@inject("graphInfoStore", "appStore", "graphStore")
@observer class GraphInfo extends Component<GraphInfoProps, {}> {
	render() {
		const graphInfoStore: GraphInfoStore = this.props.graphInfoStore;
		const appStore: AppStore = this.props.appStore;
		const graphStore: GraphStore = this.props.graphStore;
		
		const graphInfo: any = graphStore.graphInfo;

		const open: string = graphInfoStore.open ? 'open' : '';

		const GenCardItem = ({ value, title, unit }: { value: string | number, title: string, unit?: string }) => {
			if(value){
				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value } { unit ? unit : null }</div>
					</div>
				);
			}else{
				return null;
			}
		}

		return (
			<div className={ `info-card ${appStore.theme} ${open}` }>
		    <Scrollbars autoHide style={{ height: '100%' }}>
			    <div className="info-card__close" onClick = { graphInfoStore.setClose }>
			    	<img src={iconClose} alt="" />
			    </div>
			    <div className="info-card__header">
			      <div className="info-card__subtitle">
			        Наименование товара
			      </div>
			      <div className="info-card__title">
			      	{ graphInfo.P_NAME }
			      </div>
			    </div>
			    <div className="info-card__info">
			      <div className="info-card__info-item">
			      	<span>РНПТ:</span> { graphInfo.IPT }
			      </div>
			    </div>
			    <div className="info-card__section">
			      <GenCardItem title='Код по ТН ВЭД' value={ graphInfo.TNVED }/>
			      <GenCardItem title='Код по ОКПД2' value={ graphInfo.OKPD2_LIST }/>
			      <GenCardItem title='Количество товара, выпущенного по ДТ' value={ graphInfo.IMP_CNT } unit='ед.'/>
			      <GenCardItem title='Количество товара, выбывшего из прослеживаемости' value={ graphInfo.OUT_CNT } unit='ед.'/>
			      <GenCardItem title='Количество товара, возобновленного в прослеживаемость' value={ graphInfo.IN_CNT } unit='ед.'/>
			      <GenCardItem title='Общий остаток товара в прослеживаемости' value={ graphInfo.BALANCE } unit='руб.'/>
			      <GenCardItem title='Минимальная цена' value={ graphInfo.PRICE_MIN } unit='руб.'/>
			      <GenCardItem title='Максимальная цена' value={ graphInfo.PRICE_MAX } unit='руб.'/>
			    </div>
		    </Scrollbars>
		  </div>
		);
	}
}

export default GraphInfo;
