import * as go from 'gojs';

class HorizontalLink extends go.Link {

  computeMidOrthoPosition = (fromPosition, toPosition, vertical) => fromPosition;

  computePoints = (): boolean => {
    this.clearPoints();

    const fromNode: go.Node = this.fromNode;
    const toNode: go.Node = this.toNode;

    const fromNodeChildrens: go.Iterator<go.Node> = fromNode.findNodesOutOf();
    const toNodeParents: go.Iterator<go.Node> = toNode.findNodesInto();
    const fromNodeOutLinks: go.Iterator<go.Link> = fromNode.findLinksOutOf();

    if (toNode.location.x) {
      if (fromNode.location.x === 340 && toNode.location.x === 340) {
        this.data.isFromRoot = true;
        this.diagram.commit((d) => d.model.set(this.data, "isFromRoot", true));
        let startX = fromNode.location.x + 36;
        let startY = fromNode.location.y + fromNode.height - 8;
        let endX = toNode.location.x + 36;
        let endY = toNode.location.y + 8;

        if(fromNode.findLinksBetween(toNode).count > 1 && toNode.category !== 'megaNode'){
            let isLeft, isCenter = false;

            fromNode.findLinksBetween(toNode).each((link) => {
              if(link.data.connect === 'left' && link !== this) isLeft = true; 
              if(link.data.connect === 'center' && link !== this) isCenter = true; 
            });

            if(!isLeft){
              this.data.connect = 'left';
              startX = fromNode.location.x + 100;
              endX = toNode.location.x + 100;
            }else if(!isCenter){
              this.data.connect = 'center';
              startX = fromNode.location.x + 170;
              endX = toNode.location.x + 170;
            }else{
              startX = fromNode.location.x + 30;
              endX = toNode.location.x + 30;
            }
          }

        this.addPoint(
          new go.Point(
            startX,
            startY
          )
        );

        this.addPoint(
          new go.Point(
            endX,
            endY
          )
        );
      } else {
        if (this.category !== 'potential') {
          this.addPoint(
            new go.Point(
              fromNode.location.x + fromNode.width - 8,
              fromNode.location.y + fromNode.height / 2
            )
          );

          this.addPoint(
            new go.Point(
              fromNode.location.x + fromNode.width + 20,
              fromNode.location.y + fromNode.height / 2
            )
          );

          if (fromNodeChildrens.count > 1) {
            let topCount: number = 0;
            let bottomCount: number = 0;

            fromNodeChildrens.each((node) => {
              if (node.location.y < fromNode.location.y) topCount++;
              if (node.location.y > fromNode.location.y) bottomCount++;
            });

            if ((topCount > 0 && bottomCount > 0) || fromNodeOutLinks.count > 2) {
              this.addPoint(
                new go.Point(
                  fromNode.location.x + fromNode.width + 20,
                  fromNode.location.y + fromNode.height / 2 - 1
                )
              );

              this.addPoint(
                new go.Point(
                  fromNode.location.x + fromNode.width + 20,
                  fromNode.location.y + fromNode.height / 2 + 1
                )
              );
            }
          }

          let lastPointY, lastPointX;
          if (toNodeParents.count > 1 && fromNode.location.y < toNode.location.y && toNode.category !== 'megaNode') {
            lastPointY = toNode.location.y + 5;
            lastPointX = toNode.location.x + 25;
          } else if (toNodeParents.count > 1 && fromNode.location.y > toNode.location.y && toNode.category !== 'megaNode') {
            lastPointY = toNode.location.y + toNode.height - 5;
            lastPointX = toNode.location.x + 25;
          } else {
            lastPointY = toNode.location.y + toNode.height / 2;
            lastPointX = toNode.location.x + 8;
          }

          if(fromNode.findLinksBetween(toNode).count > 1 && toNode.category !== 'megaNode'){
            let isToTop, isToCenter = false;

            fromNode.findLinksBetween(toNode).each((link) => {
              if(link.data.connect === 'toTop' && link !== this) isToTop = true; 
              if(link.data.connect === 'toCenter' && link !== this) isToCenter = true; 
            });

            if(!isToTop){
              this.data.connect = 'toTop';
              //this.diagram.commit((d) => d.model.set(this.data, "connect", 'toTop'));
              lastPointY = toNode.location.y + 5;
              lastPointX = toNode.location.x + 25;
            }else if(!isToCenter){
              this.data.connect = 'toCenter';
              //this.diagram.commit((d) => d.model.set(this.data, "connect", 'toCenter'));
              lastPointY = toNode.location.y + toNode.height / 2;
              lastPointX = toNode.location.x + 8;
            }else{
              lastPointY = toNode.location.y + toNode.height - 5;
              lastPointX = toNode.location.x + 25;
            }

            this.addPoint(
                new go.Point(
                  fromNode.location.x + fromNode.width + 20,
                  fromNode.location.y + fromNode.height / 2 - 1
                )
              );

              this.addPoint(
                new go.Point(
                  fromNode.location.x + fromNode.width + 20,
                  fromNode.location.y + fromNode.height / 2 + 1
                )
              );
          }

          this.addPoint(
            new go.Point(
              fromNode.location.x + fromNode.width + 20,
              lastPointY
            )
          );

          this.addPoint(
            new go.Point(
              lastPointX,
              lastPointY
            )
          );
        } else {
          if (fromNode.location.x === toNode.location.x) {
            if (fromNode.location.y > toNode.location.y) {
              this.addPoint(
                new go.Point(
                  fromNode.location.x + fromNode.width / 2,
                  fromNode.location.y + 8
                )
              );

              this.addPoint(
                new go.Point(
                  toNode.location.x + toNode.width / 2,
                  toNode.location.y + toNode.height - 8
                )
              );
            } else {
              this.addPoint(
                new go.Point(
                  fromNode.location.x + fromNode.width / 2,
                  fromNode.location.y + fromNode.height - 8
                )
              );

              this.addPoint(
                new go.Point(
                  toNode.location.x + fromNode.width / 2,
                  toNode.location.y + 8
                )
              );
            }
          } else if (fromNode.location.x < toNode.location.x) {
            this.addPoint(
              new go.Point(
                fromNode.location.x + fromNode.width - 8,
                fromNode.location.y + fromNode.height / 2
              )
            );

            this.addPoint(
              new go.Point(
                fromNode.location.x + fromNode.width + 20,
                fromNode.location.y + fromNode.height / 2
              )
            );

            this.addPoint(
              new go.Point(
                fromNode.location.x + fromNode.width + 20,
                toNode.location.y + toNode.height / 2
              )
            );

            this.addPoint(
              new go.Point(
                toNode.location.x + 8,
                toNode.location.y + toNode.height / 2
              )
            );
          } else if (fromNode.location.x > toNode.location.x) {
            this.addPoint(
              new go.Point(
                fromNode.location.x + 8,
                fromNode.location.y + fromNode.height / 2
              )
            );

            this.addPoint(
              new go.Point(
                fromNode.location.x - 20,
                fromNode.location.y + fromNode.height / 2
              )
            );

            this.addPoint(
              new go.Point(
                fromNode.location.x - 20,
                toNode.location.y + toNode.height / 2
              )
            );

            this.addPoint(
              new go.Point(
                toNode.location.x + toNode.width - 8,
                toNode.location.y + toNode.height / 2
              )
            );
          }
        }
      }
    }

    return true;
  };

}

export default HorizontalLink;