import React, { Component } from 'react';
import * as go from 'gojs';
import { observer, inject } from 'mobx-react';
import { Scrollbars } from 'react-custom-scrollbars';

import AppStore from "../../stores/AppStore";
import GraphStore from "../../stores/GraphStore";
import InfoCardStore from "../../stores/InfoCardStore";
import formatDate from '../../utils/formatDate';

import './infoCard.css';

//import iconArrow from '../../icons/icon_arrow.svg';
import iconClose from '../../icons/icon_close.svg';

export interface InfoCardProps { 
  appStore?: AppStore;
  graphStore?: GraphStore;
  infoCardStore?: InfoCardStore;
}

@inject("infoCardStore", "appStore", "graphStore")
@observer class InfoCard extends Component<InfoCardProps, {}> {
	render() {
		const infoCardStore: InfoCardStore = this.props.infoCardStore;
		const appStore: AppStore = this.props.appStore;
		const graphStore: GraphStore = this.props.graphStore;
		
		const graphInfo: any = graphStore.graphInfo;

		const open: string = infoCardStore.open ? 'open' : '';
		const collapsed: string = infoCardStore.collapsed ? 'collapsed' : ''

		const node: go.Node = infoCardStore.selectedNode;
		const link: go.Link = infoCardStore.selectedLink;

		const cardDataItem = (item: string): string | number => {	
			if(node && node.data[item]){
				return node.data[item];
			}else{
				return null;
			}
		}

		const GenCardItem = ({ value, title, unit }: { value: string | number, title: string, unit?: string }) => {
			if(node && value){
				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value } { unit ? unit : null }</div>
					</div>
				);
			}
			return null;
		}

		const CardItem = ({ value, title }: { value: string, title: string }) => {
			if(node && (node.data[value] || node.data[value] === 0)){
				if(value === 'ROOT' || value === 'LEAF'){
					value = node.data[value] ? 'Да' : 'Нет';
				}else if(value === 'TYPE'){
					value = graphStore.nodeTypes[node.data[value]];
				} else{
					value = node.data[value];
				}

				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value }</div>
					</div>
				);
			}else{
				return null;
			}
		}

		const CardHeader = () => {
			if(node && (type === 5 || type === 1)){
			  return (
			  	<div>
			  		<div className="info-card__header">
				      <div className="info-card__subtitle">
				        Наименование
				      </div>
				      <div className="info-card__title">
				      	{ cardDataItem('SHORT_NAME') }
				      </div>
				    </div>
				    <div className="info-card__info">
				      <div className="info-card__info-item">
				      	<span>ИНН:</span> { cardDataItem('INN') }
				      </div>
				      <div className="info-card__info-item">
				      	<span>КПП:</span> { cardDataItem('KPP') }
				      </div>
				    </div>
			  	</div>
			  )
			}
			return null;
		}

		const type = node ? node.data.TYPE : null;

		const Tax = () => {
			if(node && type === 5){
			  return <CardItem title='Режим налогообложения' value='TAX_TYPE_NAME' />
			}
			return null;
		}

		const Balance = () => {
			if(node && type === 5){
			  return <CardItem title='Остаток товара, находящегося в собственности НП' value='BALANCE' />
			}
			return null;
		}

		const BalanceBr = () => {
			if(node && type === 5){
			  return <CardItem title='Остаток товара, участвующего в посреднических операциях' value='BALANCE_BR' />
			}
			return null;
		}

		const Rntp = () => {
			if(node && (node.data.ROOT || type === 7 || type === 9 || type === 11 || type === 15 || type === 20 || type === 21
			|| type === 22 || type === 23 || type === 24 || type === 8 || type === 12 || type === 13 || type === 25
		  || type === 26 || type === 27 || type === 19)){
			  return <GenCardItem title='РНТП' value={ graphInfo.IPT } />
			}
			return null;
		}

		const Credit = () => {
			if(node && (type === 8 || type === 12 || type === 13 || type === 25
		  || type === 26 || type === 27)){
			  return <CardItem title='Количество товара, по которому возобновлена прослеживаемость' value='CREDIT' />
			}
			return null;
		}

		const Credit19 = () => {
			if(node && (type === 19)){
			  return <CardItem title='Количество ввезенного товара' value='CREDIT' />
			}
			return null;
		}

		const Debet = () => {
			if(node && (node.data.ROOT || type === 7 || type === 9 || type === 11 || type === 15 || type === 20 || type === 21
			|| type === 22 || type === 23 || type === 24)){
			  return <CardItem title='Количество товара' value='DEBET' />
			}
			return null;
		}

		const Price = () => {
			if(node && node.data.ROOT){
			  return <CardItem title='Цена товара' value='PRICE_MIN_IN' />
			}
			return null;
		}

		const Country = () => {
			if(node && (node.data.ROOT || type === 19)){
			  return <CardItem title='Страна отправления' value='CO_NAME' />
			}
			return null;
		}

		const CountryTo = () => {
			if(node && (type === 7)){
			  return <CardItem title='Страна назначения' value='CO_NAME' />
			}
			return null;
		}

		const Type = () => {
			if(node && (type === 7 || type === 9 || type === 11 || type === 15 || type === 20 || type === 21
			|| type === 22 || type === 23 || type === 24 || type === 8 || type === 12 || type === 13 || type === 25
		  || type === 26 || type === 27)){
			  return <CardItem title='Наименование вида операции по выбытию:' value='TYPE' />
			}
			return null;
		}

		const LinkCardItem = ({ value, title }: { value: string, title: string }) => {
			if(link && (link.data[value] || link.data[value] === 0)){
				if(value === 'LAST_DOC_DATE'){
					const date = new Date(+link.data[value]*1000);
					value = formatDate(date);
				}else{
					value = link.data[value];
				}
				return(
					<div className="info-card__item">
						<div className="info-card__item-title">{ title }</div>
						<div className="info-card__item-body">{ value }</div>
					</div>
				);
			}else{
				return null;
			}
		}

		return (
			<div className={ `info-card ${appStore.theme} ${open} ${collapsed}` }>
		    <Scrollbars autoHide style={{ height: '100%' }}>
			    <div className="info-card__close" onClick = { infoCardStore.setClose }>
			    	<img src={iconClose} alt="" />
			    </div>
			    <div className="info-card__show" onClick = { infoCardStore.changeCollapsed }>
			      
			    </div>
			    <CardHeader/>
			    <div className="info-card__section">
			    	<Tax/>
			    	<Balance/>
			    	<BalanceBr/>
			    	<Rntp/>
			    	<Debet/>
			    	<Credit/>
			    	<Credit19/>
			    	<Price/>
			    	<Country/>
			    	<CountryTo/>
			    	<Type/>
			    	<LinkCardItem title='Количество товара на ребре' value='QUANTITY' />
			    	<LinkCardItem title='Cтоимость товара на ребре' value='COST_RUB_GROSS' />
			    	<LinkCardItem title='Дата последней операции' value='LAST_DOC_DATE' />
			    </div>
		    </Scrollbars>
		  </div>
		);
	}
}

export default InfoCard;
