import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Scrollbars } from 'react-custom-scrollbars';

import AppStore from "../../stores/AppStore";
import LegendStore from "../../stores/LegendStore";

import './legend.css';
import iconClose from '../../icons/icon_close.svg';
import { legendIconsLight, legendIconsDark } from '../../utils/legendIcons';

export interface LegendProps { 
  appStore?: AppStore;
  legendStore?: LegendStore;
}

@inject("legendStore", "appStore")
@observer class Legend extends Component<LegendProps, {}> {
	render() {
		const legendStore: LegendStore = this.props.legendStore;
		const appStore: AppStore = this.props.appStore;
		
		const open: string = legendStore.open ? 'open' : '';

		return (
			<Scrollbars autoHide style={{ height: '100%' }}>
			<div className={ `legend ${appStore.theme} ${open}` }>
		    
			    <div className="legend__close" onClick = { legendStore.setClose }>
			    	<img src={iconClose} alt="" />
			    </div>
			    <div className="legend__col">
				    <div className="legend__section">
				    	<div className="legend__title">
				    		Другие индикаторы
				   		</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon1} alt="" /></div>
				    		<div className="legend__text">Индивидуальный предприниматель</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon2} alt="" /></div>
				    		<div className="legend__text">Зарегистрированные остатки</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon3} alt="" /></div>
				    		<div className="legend__text">Использование в производстве, в т.ч. переработке</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon4} alt="" /></div>
				    		<div className="legend__text">Уничтожение или безвозвратная утрата товаров вследствие действия непреодолимой силы</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon5} alt="" /></div>
				    		<div className="legend__text">Передача или реализация в розничной торговле</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon6} alt="" /></div>
				    		<div className="legend__text">Возврат товара из розницы</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon7} alt="" /></div>
				    		<div className="legend__text">Конфискация государством</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon8} alt="" /></div>
				    		<div className="legend__text">Фактический вывоз с таможенной территории РФ</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon9} alt="" /></div>
				    		<div className="legend__text">Пересортица</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon10} alt="" /></div>
				    		<div className="legend__text">Разгон цены</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon11} alt="" /></div>
				    		<div className="legend__text">Был человечек с мешком</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon12} alt="" /></div>
				    		<div className="legend__text">Захоронение, обезвреживание, утилизация или захоронение</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon13} alt="" /></div>
				    		<div className="legend__text">Возврат утраченного товара</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon14} alt="" /></div>
				    		<div className="legend__text">Включение в состав основных средств</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon15} alt="" /></div>
				    		<div className="legend__text">Вывод из состава основных средств</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon16} alt="" /></div>
				    		<div className="legend__text">Передача в счет погашения обязательств при банкротстве</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon17} alt="" /></div>
				    		<div className="legend__text">Передача при выделении из уставного капитала</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon18} alt="" /></div>
				    		<div className="legend__text">Передача в уставной капитал</div>
				    	</div>
				    </div>
				  </div>

				  <div className="legend__col legend__right">
				    <div className="legend__section">
				    	<div className="legend__title">
				    		Уровень риска
				   		</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon22} alt="" /></div>
				    		<div className="legend__text">Не определен</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon21} alt="" /></div>
				    		<div className="legend__text">Низкий</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon20} alt="" /></div>
				    		<div className="legend__text">Средний</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon19} alt="" /></div>
				    		<div className="legend__text">Высокий</div>
				    	</div>
				    </div>

				    <div className="legend__section">
				    	<div className="legend__title">
				    		Наличие остатка
				   		</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon23} alt="" /></div>
				    		<div className="legend__text">Критический, на узле</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon24} alt="" /></div>
				    		<div className="legend__text">Отрицательный, на узле</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon25} alt="" /></div>
				    		<div className="legend__text">У посредника</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon26} alt="" /></div>
				    		<div className="legend__text">Пиктограма остатка</div>
				    	</div>
				    </div>

				    <div className="legend__section">
				    	<div className="legend__title">
				    		Индикаторы связи
				   		</div>
				    	<div className="legend__item">
				    		<div className="legend__image arrow"><img src={legendIconsLight.icon27} alt="" /></div>
				    		<div className="legend__text">Переход права собственности</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image arrow"><img src={legendIconsLight.icon28} alt="" /></div>
				    		<div className="legend__text">Посредническая сделка</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image arrow"><img src={legendIconsLight.icon29} alt="" /></div>
				    		<div className="legend__text">Связь, к группе узлов</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image arrow"><img src={legendIconsLight.icon30} alt="" /></div>
				    		<div className="legend__text">Операции с расхождениями</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image arrow"><img src={legendIconsLight.icon31} alt="" /></div>
				    		<div className="legend__text">Дубликат НП к основному узлу</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image arrow"><img src={legendIconsLight.icon32} alt="" /></div>
				    		<div className="legend__text">Наличие риска</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image arrow"><img src={legendIconsLight.icon33} alt="" /></div>
				    		<div className="legend__text">Потенциальные контрагенты</div>
				    	</div>
				    </div>

				    <div className="legend__section">
				    	<div className="legend__item">
				    		<div className="legend__image">
				    			<img src={appStore.theme === 'dark' ? legendIconsLight.icon34 : legendIconsDark.icon34} alt="" />
				    		</div>
				    		<div className="legend__text">Скрыть\показать циклы(дубликаты)</div>
				    	</div>
				    	<div className="legend__item">
				    		<div className="legend__image"><img src={legendIconsLight.icon35} alt="" /></div>
				    		<div className="legend__text">Направление сделок</div>
				    	</div>
				    </div>

				    <div className="legend__section">
				    	<div className="legend__item">
				    		<div className="legend__image last">
				    			<img src={appStore.theme === 'dark' ? legendIconsLight.icon36 : legendIconsDark.icon36} alt="" />
				    		</div>
				    		<div className="legend__text">Группа контрагентов, начиная со второго уровня</div>
				    	</div>
				    </div>
			    </div>
		   
		  </div>
		   </Scrollbars>
		);
	}
}

export default Legend;
