import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import Draggable from 'react-draggable';
import { Scrollbars } from 'react-custom-scrollbars';

import AppStore from "../../stores/AppStore";
import RiskCardStore from "../../stores/RiskCardStore";

import formatDate from '../../utils/formatDate';

import iconClose from '../../icons/icon_close.svg';
import './riskCard.css';

export interface RiskCardProps { 
  appStore?: AppStore;
  riskCardStore?: RiskCardStore;
}

@inject("appStore", "riskCardStore")
@observer class RiskCard extends Component<RiskCardProps, {}> {
  render() {
    const appStore: AppStore = this.props.appStore;
    const riskCardStore: RiskCardStore = this.props.riskCardStore;

    const RiskRntp = () => {
      return (
        <div className="risk__item">
          <div className="risk__item-title">
            {riskCardStore.connectedRntp.length > 0 ? 'Связанные РНТП:' : ''}
          </div>
          { riskRntpItems }
        </div>
      )
    }

    const riskRntpItems = riskCardStore.connectedRntp.map((rntp, i) => {
      return(
        <div key = { `rntp_${i}` }  className="risk__item-item">{ rntp.IPT }</div>
      )
    })

    const RiskItem = ({title, value}: {title: string, value: any}) => {
      return (
        <div className="risk__item">
          <div className="risk__item-title">{title}</div>
          <div className="risk__item-item">{value}</div>
        </div>
      )
    }

    const code = riskCardStore.currentRisk ? riskCardStore.currentRisk.Code : 0;

    const InValue = () => {
      if(code === 201 || code === 203 || code === 205){
        return <RiskItem title="Входящее количество" value={riskCardStore.currentRiskNode.data.DEBET} />
      }

      return null;
    }

    const OutValue = () => {
      if(code === 201 || code === 203 || code === 205){
        return <RiskItem title="Исходщее количество" value={riskCardStore.currentRiskNode.data.CREDIT} />
      }

      return null;
    }

    const Balance = () => {
      if(code === 201 || code === 205 || code === 206){
        return <RiskItem title="Остаток на узле" value={riskCardStore.currentRiskNode.data.BALANCE} />
      }

      return null;
    }

    const MixBalance = () => {
      if(code === 206){
        return <RiskItem title="Остаток по РНТП" value={riskCardStore.currentRisk.MixBalance} />
      }

      return null;
    }

    const PriceIn = () => {
      if(code === 204){
        return <RiskItem title="Средняя цена покупки" value={riskCardStore.currentRisk.priceIn} />
      }

      return null;
    }

    const PriceOut = () => {
      if(code === 204){
        return <RiskItem title="Средняя цена продажи" value={riskCardStore.currentRisk.priceOut} />
      }

      return null;
    }

    const LastDocDate = () => {
      if(code === 204){
        const date = new Date(riskCardStore.currentRiskNode.data.LAST_DOC_DATE*1000);
        return <RiskItem title="Дата последней сделки" value={formatDate(date)} />
      }

      return null;
    }

    let riskIcon;

    if(code === 201 || code === 203){
      riskIcon = riskCardStore.currentRiskNode.data.balanceIcon;
    }else if(code === 204 || code === 205 || code === 206){
      riskIcon = riskCardStore.currentRiskNode.data.riskIcon;
    }

    return (
      <Draggable>
        <div className={ `risk ${ riskCardStore.riskCardIsOpen ? 'open' : 'close'} ${ appStore.theme }` }>
          <Scrollbars autoHide style={{ height: '240px', width: '340px' }}>
            <div className="risk__close" onClick = { () => riskCardStore.closeRiskCard() }>
              <img src={iconClose} alt="" />
            </div>
            <div className="risk__header">
              <div className="risk__image">
                <img src={riskIcon} alt="" />
              </div>
              <div className="risk__info">
                <div className="risk__title">
                  {riskCardStore.currentRisk ? riskCardStore.currentRisk.Name : ''}
                </div>
                <div className="risk__nodetitle">
                  {riskCardStore.currentRiskNode ? riskCardStore.currentRiskNode.data.SHORT_NAME : ''}
                </div>
              </div>
            </div>
            <div className="risk__inn">
              <span>ИНН:</span>{riskCardStore.currentRiskNode ? riskCardStore.currentRiskNode.data.INN : ''}
            </div>
            <div className="risk__items">
              <InValue/>
              <OutValue/>
              <Balance/>
              <RiskRntp/>
              <MixBalance/>
              <PriceIn/>
              <PriceOut/>
              <LastDocDate/>
            </div>
          </Scrollbars>
        </div>  
      </Draggable>
		);
  }
}

export default RiskCard;
