import { observable, configure, action, decorate } from 'mobx';

import GraphStore from "./GraphStore";
import MenuStore from "./MenuStore";
import RightMenuStore from "./RightMenuStore";
import InfoCardStore from "./InfoCardStore";
import RiskCardStore from "./RiskCardStore";
import ZoomStore from "./ZoomStore";
import GraphInfoStore from "./GraphInfoStore";
import LegendStore from "./LegendStore";

import { Options, Data, Events } from '../interface';

configure({ enforceActions: 'observed' });

class AppStore {
  theme: string = 'dark';
  options: Options;
  data: Data;
  events: Events;

  MenuStore: MenuStore = new MenuStore();
  RightMenuStore: RightMenuStore = new RightMenuStore();
  RiskCardStore: RiskCardStore = new RiskCardStore();
  ZoomStore: ZoomStore = new ZoomStore();
  GraphInfoStore: GraphInfoStore = new GraphInfoStore();
  LegendStore: LegendStore = new LegendStore();
  InfoCardStore: InfoCardStore;
  GraphStore: GraphStore;

  constructor(options: Options, data: Data, events: Events) {
    this.theme = options.theme !== 'light' ? 'dark' : 'light';
    this.options = options;
    this.data = data;
    this.events = events;
    this.InfoCardStore = new InfoCardStore(this);
    this.GraphStore = new GraphStore(this, this.InfoCardStore, this.RiskCardStore);
  }

  changeTheme = (): void => {
    this.theme === 'light' ? this.theme = 'dark' : this.theme = 'light';
  }

  isFullScreen: boolean = false;

  changeFullScreen = () => {
    if(!this.isFullScreen){
      document.documentElement.requestFullscreen();
    }else{
      document.exitFullscreen();
    }
    this.isFullScreen = !this.isFullScreen;
  }
};

decorate(AppStore, {
  theme: observable,
  isFullScreen: observable,
  changeTheme: action,
  changeFullScreen: action
});

export default AppStore;